import React from "react";
import ImageComponent from "./ImageComponent";
import "./Recipe.css";

export default function Recipe(props) {
  const { title, ingredients, description, image } = props.location.state;
  const ingredientsArray = ingredients.split(",");

  // const description1 = description.split(".").splice(1).join();
  // const abc = description1.replace(/\r?\n|\r/, "");
  // const newResult = abc.split(",");
  // const endResult = newResult.map((word, i) => {
  //   if (i !== newResult.length - 1) {
  //     return word.slice(0, -1);
  //   } else {
  //     return word;
  //   }
  // });

  const instructions = description.split(".");

  return (
    <div>
      <div className="recipe-wrap">
        <div className="recipe-left">
          <h2>{title}</h2>
          <div className="img-ingred">
            <ImageComponent url={image} />
            <div className="ingredients">
              {ingredientsArray.map((ingredient, i) => {
                return <li key={i}>{ingredient}</li>;
              })}
            </div>
          </div>
        </div>
        <div className="recipe-right">
          <div className="recipe-description">
            {instructions.map((instruction, i) => {
              return (
                <li key={i}>
                  <span>{i + 1}</span> {instruction}
                </li>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
