import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./Add.css";

export default function Recipe() {
  const [title, setTitle] = useState("");
  const [ingredients, setIngredients] = useState("");
  const [description, setDescription] = useState("");
  const [image, setImage] = useState("");
  const [category, setCategory] = useState("");

  //   const [recipe, setRecipe] = useState("");

  const reset = () => {
    setTitle("");
    setIngredients("");
    setDescription("");
    setImage("");
    setCategory("");
  };

  //should i remove "async" here?
  const saveRecipe = async (e) => {
    e.preventDefault();
    const newRecipe = {
      title,
      ingredients,
      description,
      image,
      category,
    };

    const oldRecipes = localStorage.getItem("recipeList");
    if (!oldRecipes) {
      const allRecipes = JSON.stringify([newRecipe]);
      localStorage.setItem("recipeList", allRecipes);
    } else {
      const recipeList = JSON.parse(oldRecipes);
      recipeList.push(newRecipe);
      localStorage.setItem("recipeList", JSON.stringify(recipeList));
    }
    reset();
  };
  return (
    <div className="recipe-container">
      <div className="add-recipe-title">
        <h2>Add a</h2>
        <h1>new</h1>
        <h2>recipe</h2>
      </div>
      <form>
        <input
          type="text"
          placeholder="recipe title"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
        <input
          type="text"
          placeholder="ingredients separated by comma"
          value={ingredients}
          onChange={(e) => setIngredients(e.target.value)}
        />
        <textarea
          placeholder="recipe instructions"
          value={description}
          rows="8"
          onChange={(e) => setDescription(e.target.value)}
        />
        <input
          type="text"
          placeholder="add link to image here"
          value={image}
          onChange={(e) => setImage(e.target.value)}
        />
        <p>Choose a category:</p>
        <div>
          <label>
            <input
              type="radio"
              name="category"
              value="sweet"
              onChange={(e) => setCategory(e.target.value)}
            />
            sweet
          </label>
          <label>
            <input
              type="radio"
              name="category"
              value="savoury"
              onChange={(e) => setCategory(e.target.value)}
            />
            savoury
          </label>
          <label>
            <input
              type="radio"
              value="other"
              name="category"
              onChange={(e) => setCategory(e.target.value)}
            />
            other
          </label>
        </div>
        <button onClick={saveRecipe}>
          <Link to="/collection" className="link">
            Add recipe
          </Link>
        </button>
      </form>
    </div>
  );
}
